.cbp-header {
  background: #003366;
  height: 80px;
}
.cbp-header-text {
  color: white;
  font-weight: 700;
}
.cbp-header i {
  margin-right: 8px;
  color: #ffffff;
}
.cbp-header .cbp-container {
  align-items: center;
  height: inherit;
  margin: auto 32px;
}
.cbp-header .usa-button, .cbp-header button {
  background-color: #003366;
}
.cbp-header .usa-button:hover, .cbp-header button:hover {
  background: rgba(255, 255, 255, 0.2);
}
.cbp-header .cbp-external-button {
  display: inline-block;
  text-decoration: none;
  color: #ffffff;
  padding: 16px;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.12em;
}
.cbp-header .cbp-dropdown-item {
  align-items: center;
  display: block;
  font-weight: 500;
  min-height: 32px;
  height: inherit;
  justify-content: left;
  color: #ffffff;
}
.cbp-header .cbp-dropdown-item > a {
  color: inherit;
  display: inherit;
  text-decoration: none;
  width: inherit;
}
.cbp-header .cbp-dropdown-item > a:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #ffffff;
}
.cbp-header .cbp-dropdown-item > a > span {
  display: inherit;
  padding: 8px 16px;
}
.cbp-header-subtext {
  display: inline-block;
  color: #59b9de;
  font-style: italic;
  margin: 16px 16px 0 16px;
}

.cbp-logo-text {
  color: white;
  font-weight: 500;
}
.cbp-logo-mobile {
  display: none;
}

.dropdown-content {
  background: #003366;
  border-radius: 0 0 5px 5px;
  display: none;
  margin-top: 8px;
  min-width: 12em;
  max-width: 15em;
  position: absolute;
  z-index: 1;
}
.dropdown-content.show {
  display: block;
}

ul {
  padding: 0;
  margin-top: 8px;
}

li {
  list-style-type: none;
  height: auto;
  min-height: 32px;
}

.dropdown-header-mobile {
  display: none;
}
.dropdown-header-mobile .usa-button:hover, .dropdown-header-mobile .cbp-header button:hover, .cbp-header .dropdown-header-mobile button:hover {
  background: transparent;
}

@media (max-width: 1024px) {
  button > .cbp-header-text,
a > .cbp-header-text {
    display: none;
  }

  .dropdown-content {
    background: #003366;
    display: none;
    height: 100vh;
    left: 0;
    max-width: 100vw;
    padding: 32px;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 1;
  }
  .dropdown-content.show {
    display: block;
  }

  .usa-button, .cbp-header button {
    max-width: 100px;
    text-align: right;
  }
  .usa-button .cbp-close-icon, .cbp-header button .cbp-close-icon {
    color: white;
    font-size: 16px;
  }

  .dropdown-header-mobile {
    display: flex;
  }
  .dropdown-header-mobile h2 {
    font-weight: normal;
    font-size: 2.027rem;
    line-height: 36px;
    color: #ffffff;
  }

  .cbp-dropdown-item:hover {
    background: transparent;
  }
}
@media (max-width: 54em) {
  ul {
    margin-left: 24px;
  }

  .cbp-dropdown-item {
    justify-content: left;
  }

  .icons-mobile {
    display: flex;
  }

  .cbp-header-subtext {
    margin: 32px 32px 8px 36px;
  }

  .cbp-external-button {
    display: block;
    text-decoration: none;
    margin: 16px auto auto auto;
    color: #ffffff;
  }

  .dropdown-content {
    margin: 0;
  }

  .cbp-dropdown-item:hover {
    background: transparent;
  }

  .dropdown-content {
    background: #003366;
    display: none;
    height: 100vh;
    left: 0;
    max-width: 100vw;
    padding: 32px;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 1;
  }
  .dropdown-content.show {
    display: block;
  }

  .usa-button, .cbp-header button {
    max-width: 100px;
    text-align: right;
  }
  .usa-button .cbp-close-icon, .cbp-header button .cbp-close-icon {
    color: white;
    font-size: 16px;
  }

  .dropdown-header-mobile {
    display: flex;
  }
  .dropdown-header-mobile h2 {
    font-weight: normal;
    font-size: 2.027rem;
    line-height: 36px;
    color: #ffffff;
  }
}
@media (max-width: 640px) {
  .cbp-logo {
    display: none;
  }
  .cbp-logo-mobile {
    display: flex;
  }
  .cbp-logo-text {
    margin: auto;
    padding-left: 16px;
    font-weight: normal;
    font-size: 1.602rem;
    line-height: 28px;
  }
}
/* Variables */
.cbp-row {
  display: flex;
  flex: 0 0 100%;
  flex-wrap: wrap;
  height: fit-content;
}

@media only screen and (max-width: 599px) {
  .cbp-grid-container {
    margin: 0 12px;
  }

  .cbp-col {
    flex: 0 0 calc(100% - 16px);
    margin: 0 8px;
  }

  .cbp-grid-col-1-sm {
    margin: 0 8px !important;
    width: calc(25% - 16px) !important;
  }

  .cbp-grid-col-2-sm {
