  height: 100vh;
}
#cbp-grid-demo .cbp-row:first-child {
  margin-top: 1rem;
}
#cbp-grid-demo .cbp-row > div {
  background-color: #e1e7f1;
  border: 1px solid;
  margin-bottom: 1rem !important;
  padding: 1rem;
  text-align: center;
}
@media only screen and (max-width: 599px) {
  #cbp-grid-demo span::after {
    content: ".cbp-grid-col-4-sm";
  }
}
@media only screen and (min-width: 600px) and (max-width: 1023px) {
  #cbp-grid-demo span::after {
